.ctk-date-time-picker {
  width: 100%;
  margin: 0 auto;
  text-align: left;
  font-size: 14px;
  border-radius: 4px;
  position: relative;
}
.ctk-date-time-picker * {
    box-sizing: border-box;
}
.ctk-date-time-picker .time-picker-overlay {
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.ctk-date-time-picker .time-picker-overlay.has-background {
      background: rgba(0, 0, 0, 0.4);
}
.ctk-date-time-picker .field {
    position: relative;
}
.ctk-date-time-picker .field .field-label {
      position: absolute;
      top: 3px;
      cursor: pointer;
      left: 13px;
      -webkit-transform: translateY(25%);
      transform: translateY(25%);
      opacity: 0;
      transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
      font-size: 11px;
      color: rgba(0, 0, 0, 0.54);
}
.ctk-date-time-picker .field .field-input {
      cursor: pointer;
      background-color: #FFF;
      transition-duration: 0.3s;
      position: relative;
      width: 100%;
      height: 42px;
      min-height: 42px;
      padding: 0 12px;
      font-weight: 300;
      -webkit-appearance: none;
      outline: none;
      border: 1px solid rgba(0, 0, 0, 0.2);
      border-radius: 4px;
      font-size: 14px;
      z-index: 0;
}
.ctk-date-time-picker .field.has-error .field-input {
      border-color: orangered !important;
}
.ctk-date-time-picker .field.has-error .field-label {
      opacity: 1;
      -webkit-transform: translateY(0);
      transform: translateY(0);
      font-size: 11px;
}
.ctk-date-time-picker .field.has-error .field-input {
      padding-top: 14px;
}
.ctk-date-time-picker .field.has-value .field-label {
      opacity: 1;
      -webkit-transform: translateY(0);
      transform: translateY(0);
      font-size: 11px;
}
.ctk-date-time-picker .field.has-value .field-input {
      padding-top: 14px;
}
.ctk-date-time-picker .field.is-focused .field-input {
      border-color: dodgerblue;
}
.ctk-date-time-picker .field.is-focused .field-label {
      color: dodgerblue;
}
.ctk-date-time-picker .field.is-disabled .field-input {
      border-color: #CCC;
      background: #F2F2F2;
}
.ctk-date-time-picker .field.is-disabled .field-label, .ctk-date-time-picker .field.is-disabled .field-input {
      cursor: default;
}
.ctk-date-time-picker .text-danger {
    color: orangered !important;
}
.ctk-date-time-picker.is-dark .field-label {
    color: #ffffffb3;
}
.ctk-date-time-picker.is-dark .field-input {
    background-color: #424242;
    border-color: #ffffffb3;
    color: #ffffffb3;
}
.ctk-date-time-picker.is-dark ::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #ffffffb3;
}
.ctk-date-time-picker.is-dark :-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #ffffffb3;
}
.ctk-date-time-picker.is-dark ::-ms-input-placeholder {
    /* Microsoft Edge */
    color: #ffffffb3;
}
.ctk-date-time-picker.is-dark ::placeholder {
    /* Most modern browsers support this now. */
    color: #ffffffb3;
}
@media screen and (max-width: 415px) {
.time-picker-overlay {
    display: none;
}
.ctk-date-time-picker:not(.inline) {
    position: inherit !important;
}
}



